/* CustomCard */

.button {
  width: 100%;
  background-color: #776f60;
}
.button:hover {
  background-color: rgba(119, 111, 96, 0.7) !important;
}
.button:active {
  background-color: rgba(119, 111, 96, 0.7) !important;
  box-shadow: 0px 0px 5px rgba(119, 111, 96, 0.7) !important;
}

/***************/

/* CustomInput */

.customInput {
  width: 100%;
  border: rgba(119, 111, 96, 0.5) 1px solid;
  background-color: #f6f6f4;
  color: #776f60;
}
.customInput:active {
  border: #776f60 1px solid;
}
.customInput:focus {
  border: #776f60 1px solid;
  box-shadow: 0px 0px 2px rgba(119, 111, 96, 0.5);
}
.customInput:hover {
  border: #776f60 1px solid;
}

/***************/

/* ImageCheckBox */

.imageCheckboxes {
  display: flex;
  flex-wrap: wrap;
  gap: 10px; /* Отступ между чекбоксами */
}

.imageCheckboxLabel {
  display: block;
  position: relative;
  cursor: pointer;
}

.imageCheckbox {
  display: none;
}

/* Контейнер для изображения с относительным позиционированием */
.imageCheckbox + .imageContainer {
  position: relative;
}

/* Стандартная рамка для контейнера изображения */
.imageCheckbox + .imageContainer img {
  display: block;
  width: 100%; /* или другой нужный размер */
  height: auto;
  border: 2px solid transparent;
  transition: border-color 0.3s;
}

/* Затемнение фотографии и рамка при выборе чекбокса */
.imageCheckbox:checked + .imageContainer img {
  border-color: #007bff;
}

/* Псевдоэлемент для затемнения */
.imageCheckbox:checked + .imageContainer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* полупрозрачный черный фон */
  pointer-events: none;
}

/* Псевдоэлемент для галочки */
.imageCheckbox:checked + .imageContainer::after {
  content: '✓';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2em;
  color: white;
  pointer-events: none;
}
/*****************/

/* PasswordInput */

.passwordInput {
